import React from "react";
import { Redirect } from "react-router-dom";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Profile from "routes/Profile";
import Auth from "../routes/Auth";
import Home from "../routes/Home";
import Navigation from "./Navigation";
import DoProko from "routes/DoProko";
import DoSoty from "routes/DoSoty";
import DoSakuga from "routes/DoSakuga";
import Special from "routes/Special";
import Adorka from "routes/Adorka";
import LineOfAction from "routes/LineOfAction";
import Twitter from "routes/twitter";
import ChaDesign from "routes/ChaDesign";
import ChaDesignDraw from "routes/ChaDrawing";

const AppRouter=({isLoggedIn,userObj,isAndroid}) => {
    
    return (
        <Router>
            {isLoggedIn &&<Navigation isAndroid={isAndroid}/>}
            <Switch>
                
                {isLoggedIn ?(
                    <>
                        <Route exact path= "/">
                            <Home userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/profile">
                            <Profile userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/doProko">
                            <DoProko userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/doSoty">
                            <DoSoty userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/doSakugaBooru">
                            <DoSakuga userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/special">
                            <Special userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/adorka">
                            <Adorka userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/lineofaction">
                            <LineOfAction userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/twitter">
                            <Twitter userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/chadesign">
                            <ChaDesign userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Route exact path= "/chadesigndraw">
                            <ChaDesignDraw userObj={userObj} isAndroid={isAndroid}/>
                        </Route>
                        <Redirect from={"*"} to={"/"} />
                    </>
                 ) : (
                     <>
                    <Route exact path= "/">
                          <Auth /> 
                    </Route>
                    <Redirect from={"*"} to={"/"} />
                    </>
                 )}
            </Switch>
        </Router>
    )
}
export default AppRouter; 