import { dbService } from "fbase";
import React, { useEffect, useRef,useState } from "react";
import CanvasDraw from "../canvasDrawIndex";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import   firebase from "firebase/compat/app";

const CDsubmit =({DsubmitObj, isOwner,viewDeleyToggle})=>{
    const [editing,setEditing]= useState(false);
    const [newDsubmit,setnewDsubmit]=useState(DsubmitObj.text);
    // const [givedHeart,setGivedHeart]=useState(false)
    // const [yourheart,setYourHeart]=useState(DsubmitObj.heart);
    const canvasRef2 = useRef();
    const [onOff,setOnOff]=useState(true);
    // const [onReview,setOnReview]= useState(false);
    // const [offReview,setoffReview]= useState(false);
    // // canvasRef2.current.loadSaveData(DsubmitObj.text);
    let uploadDay= new Date(DsubmitObj.createdAt);
    const [viewRefPin,setViewRefpin]=useState(false);
    // useEffect(() => {
    //    
    //     //  if(viewsToggle && onOff){
    //     //     canvasRef2.current.loadSaveData(DsubmitObj.text,true);
    //     //     setOnOff(false)
    //     //  }
        
    // },[editing]);
    const onDeleteClick= () => {
        const ok = window.confirm("wanna delete this?")
        if(ok){
            dbService.doc(`Dsubmit/${DsubmitObj.id}`).delete();
            
        }
    };
    const toggleReview=()=> {
        canvasRef2.current.clear();
        // canvasRef2.current.loadSaveData(DsubmitObj.text);
        canvasRef2.current.loadSaveData(DsubmitObj.text);
        
    };
    const offReview=()=>{
        setEditing(false);
            //  
            // console.log('none')
            // const dataUrl=canvasRef2.current.getDataURL();
        
    
        
            // dbService.doc(`Dsubmit/${DsubmitObj.id}`).update({
            
            // dataUrl:dataUrl,
           //  refPhoto:{refPhoto},
            //pinNumber:randomPinNum,
        // });
        
    }
    const onReview=()=> {setEditing(true);
        setTimeout(function() {
            canvasRef2.current.loadSaveData(DsubmitObj.text);
            
            
          }, 5);
        }
    const onSubmit = (event)=> {
        event.preventDefault();
        const data = canvasRef2.current.getSaveData();
        const dataUrl=canvasRef2.current.getDataURL();
        dbService.doc(`Dsubmit/${DsubmitObj.id}`).update({
            text:data,
            dataUrl:dataUrl
        });
        setEditing(false);

    }
    const onChange = (event)=> {
        const {target:{value},
        }=event;
        setnewDsubmit(value);
    }
    //const toggleheart=()=> setYourHeart(prev => !prev);
    const onHeartClick= async() =>{
        setOnOff(prev => !prev);
        await dbService.doc(`Dsubmit/${DsubmitObj.id}`).update({
           heart:(1+(DsubmitObj.heart)),
        })
        console.log(DsubmitObj.heart)
        const pinDocRef= dbService.doc(`RefDB/refStrings`);
        pinDocRef.update({
            heart:firebase.firestore.FieldValue.arrayUnion(DsubmitObj.refPinUrl)
        });
    }


    
     
    return(
        <div   style={{float:'left', border: '10px solid rgba(0, 0, 0, 0.05)',width:"420px"
        }}>
            
        {
            editing ? (
            <>
            <button onClick={onSubmit}>Edit</button>
            {/* <form onSubmit={onSubmit}><input value={newDsubmit} required onChange={onChange}/>
            <input type={"submit"} value="edit"/>
            </form> */}
            
            <div><CanvasDraw 
            // style={{ touchAction: 'disabled'  ? 'auto' : 'none' }}
            ref={canvasRef2}
            //  disabled
            // hideInterface
            brushRadius={0.6}
              lazyRadius={0.5}
              
              brushColor={"#df6161"}
              allowOnlyPointerType={"pen"}
             loadTimeOffset={1}
             /></div>
             
             <button onClick={toggleReview}>Rewind</button>
             <button onClick={offReview}>Cancel</button>
             
            </>
            ):(
            <>
            
            <div style={{margin: '0 auto'}}>
            <LazyLoadImage
            
      
      height={400}
      src={DsubmitObj.dataUrl} // use normal <img> attributes as props
       />
       
      {/* <img src={DsubmitObj.dataUrl} height="400px" ></img> */}
      </div>
            
            
        {isOwner ? (
            <>

            {/* <button disabled> {DsubmitObj.heart}</button> */}
            {/* <button onClick={onHeartClick} style={{padding: "0px 0px"}}>{DsubmitObj.heart}</button> */}
            <button onClick={onHeartClick} style={{padding: "0px 0px"}}>{DsubmitObj.heart==0 ? (<></>):(<>+{DsubmitObj.heart}</>)}</button>
            <button onClick={onDeleteClick}>Delete</button>
            <button onClick={onReview}>Review</button>
            
            
            </>
        ):(
            <>
            <button onClick={onHeartClick}> {DsubmitObj.heart}</button>
            
            </>
        )}

            
            
                
            
            
            
            
        </>
            
            
            
            

            )}
            <button onClick={()=>{setViewRefpin(pre=>!pre)}}>Ref Pin</button>
            {"   "}  {uploadDay.toLocaleString(navigator.language || navigator.userLanguage)}
            {viewRefPin ? (<>
                <div><iframe  src={"https://assets.pinterest.com/ext/embed.html?id="+DsubmitObj.refPinUrl} height="600" width="300" frameBorder={0} scrolling="no" ></iframe>
                </div></>):(<></>)}
    </div>
)
};
    

export default CDsubmit;