import { dbService, storageService } from "fbase";
import React, { useEffect, useRef, useState } from "react";
import CanvasDraw from "../../src/canvasDrawIndex";
import firebase from "firebase/compat/app";
import PinchZoomPan from "PinchZoomPan";
import Hotkeys from "react-hot-keys";




export default ({ userObj }) => {
  const [Csubmit, setCsubmit] = useState("");
  // const [Csubmits,setCsubmits]= useState([]);
  const [pinReload, setPinreload] = useState(0);
  const canvasRef3 = useRef();
  const [drawSetting, setDrawSetting] = useState({

    brushRadius: 1,
    lazyRadius: 0,




  })


  const [twitterUrl, setTwitterUrl] = useState("HourlyLizards");


  const [brushColor, setBrushColor] = useState("#444")
  const [isBookmarked, setIsBookmarked] = useState(true)
  const [isListOn, setIsListOn] = useState(false)
  const [isListDelOn, setIsListDelOn] = useState(false)
  const [bookmarkList, setBookmarkList] = useState([])



  useEffect(() => {
    dbService.doc("usersetting/" + userObj.uid).get().then((doc) => {
      if (doc.exists) {
        setDrawSetting({
          brushRadius: doc.data().brushRadius,
          lazyRadius: doc.data().lazyRadius,
        })

        console.log(doc.data())
        //  
        setBookmarkList(doc.data().twitterBookmark)

        console.log(doc.data().twitterBookmark)
        console.log(bookmarkList)




      } else {
        // doc.data() will be undefined in this case
        console.log("No such setting document!");
      }
    }).catch((error) => {
      console.log("Error getting document:", error);

    });




  }, []);





  const refreshAndSubmit = () => {




    const data = canvasRef3.current.getSaveData();
    const dataUrl = canvasRef3.current.getDataURL();
    canvasRef3.current.clear();


    dbService.collection("Csubmit").add({
      text: data,
      createdAt: Date.now(),
      creatorId: userObj.uid,
      drawingTime: 0,
      heart: (0),
      dataUrl: dataUrl,
      refPinUrl: "",

      //  refPhoto:{refPhoto},
      //pinNumber:randomPinNum,
    });

    setCsubmit("");

    //clearInterval(timeId.current);
  }





  const onSubmit = (event) => {
    event.preventDefault();

    refreshAndSubmit();
  };




  const onClearClick = () => {
    const ok = window.confirm("wanna clear this?")
    if (ok) {
      canvasRef3.current.clear();

    }
  };
  const onUndoClick = () => {
    canvasRef3.current.undo();
  };
  const onEraseClick = () => {
    setBrushColor("erase")

  };
  const onBrushClick = () => {
    setBrushColor("#444")

  };
  const onShadowClick = () => {
    setBrushColor("shadow")

  };
  const onSubmitTwitter = (event) => {
    event.preventDefault();
    setPinreload(pinReload + 1);
    if (!bookmarkList.includes(twitterUrl)) { setIsBookmarked(false) } else { setIsBookmarked(true) }

  };
  const onTwitterListClick = (event) => {
    event.preventDefault();
    console.log(bookmarkList)
    setIsListOn(!isListOn)

  };
  const onTwtterBookmark = () => {
    
    //      
    if (!bookmarkList.includes(twitterUrl)) {

      const pinDocRef = dbService.collection("usersetting").doc(userObj.uid);
      pinDocRef.update({
        twitterBookmark: firebase.firestore.FieldValue.arrayUnion(twitterUrl)
      });
      // let add = bookmarkList.push(twitterUrl)
      // console.log(add)
      setBookmarkList([twitterUrl, ...bookmarkList])
    } else {
      setIsBookmarked(true)
    }


  };

  useEffect(() => {
    const script = document.createElement("script");
    script.setAttribute("src", "https://platform.twitter.com/widgets.js");
    document.getElementsByClassName("twitter-timeline")[0].appendChild(script);
  }, [pinReload]);

  return (
    <><Hotkeys
      keyName="ctrl+z,c,x"
      onKeyDown={(keyName, e, handle) => {
        //console.log("test:onKeyDown", keyName);
        if (keyName == "ctrl+z") { onUndoClick(); }
        if (keyName == "c") {
          if (brushColor != "erase") { onEraseClick(); }
          else if (brushColor == "erase") { onBrushClick(); }
        }
        if (keyName == "x") {
          if (brushColor != "shadow") { onShadowClick(); }
          else if (brushColor == "shadow") { onBrushClick(); }
        }
      }}
    ></Hotkeys>
      <div>

        <div style={{ float: 'right' }}>

          {/* <button onClick={ongetUrl}>geturls</button>
    <button onClick={submitsoty}>submitsoty</button>
     */}
        </div>






        <div>





          <form style={{ float: 'left' }} onSubmit={onSubmitTwitter}>
            <input value={twitterUrl} onChange={e => {
              setTwitterUrl(e.target.value);

            }} type={"text"} placeholder="@name" />
            <input type={"submit"} value="Go" />

          </form>
          <button onClick={onTwitterListClick}>list</button>
          {isBookmarked ? (<></>) : (<><button onClick={e => {
            e.preventDefault()
            onTwtterBookmark()
            setIsBookmarked(true)

          }}></button></>)}


          {isListOn ? (<>
            <button onClick={e => { e.preventDefault(); setIsListDelOn(!isListDelOn) }}></button>
            {isListDelOn ? (<>

              {bookmarkList.map(item => (<>
                <button onClick={e => {
                  e.preventDefault();

                  const pinDocRef = dbService.collection("usersetting").doc(userObj.uid);
                  pinDocRef.update({
                    twitterBookmark: firebase.firestore.FieldValue.arrayRemove(item)
                  });
                  let filtered 
                  = bookmarkList.filter((element) => element !== item);
                  setBookmarkList(filtered)
                }}>{item}</button>
              </>))}

            </>) : (<>
            
              {bookmarkList.map(item => (<>
              <button onClick={e => {
                e.preventDefault();
                setTwitterUrl(item)
              }}>{item}</button>


            </>))}
            
            </>)}

            
            
          </>) : (<>

          </>)}

        </div>


        {/* <div>https://assets.pinterest.com/ext/embed.html?id={randomPinNum[pinID]}    {pinID}</div> */}
        <div style={{ float: 'left' }}>
          {/* <iframe key={pinReload} src={"https://www.youtube.com/embed/videoseries?list="+youtubeUrls[youtubeUrlIndex]+"&cc_load_policy=1&cc_lang_pref=ko"} height={450} width={450} frameBorder={0} scrolling="no" ></iframe> */}

          <div key={pinReload} style={{ width: '450px', height: '770px' }}>


            <a class={"twitter-timeline"} data-width={"450"} data-height={"770"} href={"https://twitter.com/" + twitterUrl + "?ref_src=twsrc%5Etfw"}></a>


            {/* <script async src={"https://platform.twitter.com/widgets.js"} charset="utf-8"></script> */}
          </div>


        </div>

        <div style={{ float: 'left' }}>
          <div>



            <div style={{ float: 'right' }}>
              <label>Lazy:</label>
              <input
                type="number"
                min="0"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.lazyRadius}
                onChange={e => setDrawSetting(prevState => ({ ...prevState, lazyRadius: e.target.value }))} />
            </div>
            <div style={{ float: 'right' }}>
              {brushColor == "erase" ? (<>
                <label>Erase Size:</label>
                <input
                  type="number"
                  min="1"
                  max="100"
                  style={{ width: "30px" }}
                  value={drawSetting.eraseRadius}
                  onChange={e => setDrawSetting(prevState => ({ ...prevState, eraseRadius: e.target.value }))} />

              </>) : (<>
                <label>Brush Size:</label>
                <input
                  type="number"
                  min="1"
                  max="100"
                  style={{ width: "30px" }}
                  value={drawSetting.brushRadius}
                  onChange={e => setDrawSetting(prevState => ({ ...prevState, brushRadius: e.target.value }))} />

              </>)}
            </div>
            <div style={{ float: 'right' }}>
              <button onClick={onClearClick}>Clear Canvas</button>
            </div>
            <div style={{ float: 'right' }}>
              <button onClick={onUndoClick}></button>
            </div>

            <div style={{ float: 'right' }}>
              {(brushColor == "erase") ? (
                <><button onClick={onBrushClick}>brush</button></>
              ) : (<><button onClick={onEraseClick}>erase</button></>)}


            </div>
            <div style={{ float: 'right' }}>
              {(brushColor == "shadow") ? (
                <><button onClick={onBrushClick}>brush</button></>
              ) : (<><button onClick={onShadowClick}>shadow</button></>)}
              {brushColor}

            </div>


            <div style={{ float: 'right' }}>

              <form onSubmit={onSubmit}>

                {/* <input value={Csubmit} onChange= {onChange} type={"text"} placeholder="what'on your mind?" maxLength={120} /> */}
                <input type={"submit"} value="Upload Drawing" />

              </form>
            </div>


          </div>
          <div style={{ float: 'right' }}>
            <CanvasDraw
              canvasWidth={600}
              canvasHeight={600}
              brushRadius={(brushColor == "erase") ? drawSetting.eraseRadius : drawSetting.brushRadius}
              lazyRadius={drawSetting.lazyRadius}
              ref={canvasRef3}
              brushColor={brushColor} />
          </div>
        </div>




      </div></>
  )
};